.navigation_list_element  {
    display: inline;
    padding: 10px;
    margin: auto;
}

.navigation_list  {
    text-align: center;
}

.navigation_link {
    color: antiquewhite;
    font-size: 16px;
    font-weight: bold;
    font-family: "Segoe UI";
}

.navigation_link:hover {
    color: #006dbf;
    text-decoration: none;
}

.footer {
    margin-top: 90px;
    width: 100%;
    height: auto;
    background-color: #41abe1;
    position: fixed;
    bottom: 0%;
}
h5 {
    display: flex;
    justify-content: center;
}
.thumbnail-wrapper {
    width: auto;
    border-width: 1px;
    border-style: solid;
    border-color: black;
}

.thumbnail {
    width: auto;
    height: 150px;
}
.container_images {
    position: relative;
    margin-left: 20%;
    margin-right: 20%;
}

.collection_images {
    display: flex;
    justify-content: space-around;
    flex-flow: wrap;
    overflow: auto;
}

.single_image {
    margin: 5px;
}

.hidden {
    display: none;
}

h4 {
    display: flex;
    justify-content: center;
}

.tagsLink {
    display: inline;
}
h1 {
    text-align: center;
}
